<template>
	<view>
		<view class="status" :style="{position:headerPosition,top:statusTop}"></view>
		<view class="header" :style="{position:headerPosition,top:headerTop}">
			<view class="addr"></view>
			<view class="input-box"> </view>
			<view class="icon-btn">
				<view class="icon tongzhi" @tap="toMsg"></view>
				<view class="icon setting" @tap="toSetting"></view>
			</view>
		</view>
		<!-- 占位 -->
		<view class="place"></view>
		<!-- 用户信息 -->
		<view class="user">
			<!-- 头像 -->
			<!-- <view class="left">
				<image :src="user.face" @tap="toSetting"></image>
			</view> -->
			<!-- 微信小程序头像 -->
			<view class="headView">
				<open-data class="icon" type="userAvatarUrl"></open-data>
				<view class="icon"></view>
				<view class="iconBorder"></view>
			</view>
			<!-- 昵称,个性签名 -->
			<!-- <view class="right">
				<view class="username" @tap="toLogin">{{user.username}}</view>
				<view class="signature" @tap="toSetting">{{user.signature}}</view>
			</view> -->
			<!-- 微信小程序昵称,个性签名 -->
			<view class="right">
				<view class="username">
					<open-data type="userNickName"></open-data>
				</view>
				<view class="signature">
					性别：
					<open-data type="userGender" lang="zh_CN"></open-data>
				</view>
			</view>
			<!-- 二维码按钮 -->
			<view class="erweima" @tap="toMyQR">
				<view class="icon qr"></view>
			</view>
		</view>
		<!-- VIP banner -->
		<view class="VIP">
			<view class="title">我的订单</view>
			<view class="tis">全部订单></view>
		</view>
		<!-- 订单-余额 -->
		<view class="order">
			<!-- 订单类型 -->
			<view class="list">
				<view class="box" v-for="(row,index) in orderList" :key="index" @tap="toOrderList(index)">
					<view class="img">
						<view class="icon" :class="row.icon"></view>
					</view>
					<view class="text">{{row.text}}</view>
				</view>
			</view>
		</view>
		<view class="order">
			<!-- 余额 -->
			<view class="balance-info">
				<view class="left">
					<view class="box">
						<view class="num">{{user.integral}}</view>
						<view class="text">优惠券</view>
					</view>
					<view class="box">
						<view class="num">{{user.envelope}}<label style="font-size: 26upx;">万</label></view>
						<view class="text">最高额度</view>
					</view>
					<view class="box">
						<view class="num">{{user.balance}}</view>
						<view class="text">礼品卡</view>
					</view>
				</view>
				<view class="right">
					<view class="box" @tap="toDeposit">
						<view class="img">
							<view class="icon chongzhi"></view>
						</view>
						<view class="text">我的钱包</view>
					</view>
				</view>
			</view>
		</view>
		<!-- 工具栏 -->
		<!-- <view class="toolbar">
			<view class="title">我的工具栏</view>
			<view class="list">
				<view class="box" v-for="(row,index) in mytoolbarList" :key="index" @tap="toPage(row.url)">
					<view class="img">
						<image :src="row.img"></image>
					</view>
					<view class="text">{{row.text}}</view>
				</view>
			</view>
		</view> -->
		<!-- 占位 -->
		<view class="place-bottom"></view>
	</view>
</template>
<script>
	export default {
		data() {
			return {
				isfirst: true,
				headerPosition: "fixed",
				headerTop: null,
				statusTop: null,
				//个人信息,
				user: {
					username: '游客1002',
					face: '../../static/img/face.jpg',
					signature: '点击昵称跳转登录/注册页',
					integral: 0,
					balance: 0,
					envelope: 20
				},
				// 订单类型
				orderList: [{
						text: '待付款',
						icon: "fukuan"
					},
					{
						text: '待收货',
						icon: "shouhuo"
					},
					{
						text: '待评价',
						icon: "pingjia"
					},
					{
						text: '退换修',
						icon: "tuihuo"
					}
				],
				// 工具栏列表
				mytoolbarList: [{
						url: '../user/keep/keep',
						text: '我的收藏',
						img: '../../static/img/user/point.png'
					},
					{
						url: '../user/coupon/coupon',
						text: '优惠券',
						img: '../../static/img/user/quan.png'
					},
					{
						url: '',
						text: '新客豪礼',
						img: '../../static/img/user/renw.png'
					},
					{
						url: '',
						text: '领红包',
						img: '../../static/img/user/momey.png'
					},

					{
						url: '../user/address/address',
						text: '收货地址',
						img: '../../static/img/user/addr.png'
					},
					{
						url: '',
						text: '账户安全',
						img: '../../static/img/user/security.png'
					},
					{
						url: '',
						text: '银行卡',
						img: '../../static/img/user/bank.png'
					},
					{
						url: '',
						text: '抽奖',
						img: '../../static/img/user/choujiang.png'
					},
					// {text:'客服',img:'../../static/img/user/kefu.png'},
					// {text:'签到',img:'../../static/img/user/mingxi.png'}

				]
			}
		},
		//下拉刷新，需要自己在page.json文件中配置开启页面下拉刷新 "enablePullDownRefresh": true
		onPullDownRefresh() {
			setTimeout(function() {
				uni.stopPullDownRefresh();
			}, 1000);
		},
		onPageScroll(e) {
			//兼容iOS端下拉时顶部漂移
			this.headerPosition = e.scrollTop >= 0 ? "fixed" : "absolute";
			this.headerTop = e.scrollTop >= 0 ? null : 0;
			this.statusTop = e.scrollTop >= 0 ? null : -this.statusHeight + 'px';
		},
		onLoad() {
			this.statusHeight = 0;
			// #ifdef APP-PLUS
			this.statusHeight = plus.navigator.getStatusbarHeight();
			// #endif
		},
		onReady() {
			//此处，演示,每次页面初次渲染都把登录状态重置
			uni.setStorage({
				key: 'UserInfo',
				data: false,
				success: function() {},
				fail: function(e) {}
			});
		},
		onShow() {
			uni.getStorage({
				key: 'UserInfo',
				success: (res) => {
					if (!res.data) {
						if (this.isfirst) {
							//this.toLogin();
						}
						return;
					}
					this.user = res.data;
				},
				fail: (e) => {
					//this.toLogin(); 
				}
			});
		},
		methods: {
			//消息列表
			toMsg() {
				uni.navigateTo({
					url: '../msg/msg'
				})
			},
			toOrderList(index) {
				uni.navigateTo({
					url: '../user/order_list/order_list?tbIndex=' + index
				})
			},
			toSetting() {
				uni.navigateTo({
					url: '../user/setting/setting'
				})
			},
			toMyQR() {
				uni.navigateTo({
					url: '../user/myQR/myQR'
				})
			},
			toLogin() {
				uni.showToast({
					title: '请登录',
					icon: "none"
				});
				uni.navigateTo({
					url: '../login/login'
				})
				this.isfirst = false;
			},
			isLogin() {
				//实际应用中,用户登录状态应该用token等方法去维持登录状态.
				const value = uni.getStorageSync('UserInfo');
				if (value) {
					return true;
				}
				return false
			},
			toDeposit() {
				uni.navigateTo({
					url: '../user/deposit/deposit'
				})
			},
			toPage(url) {
				console.log("url: " + url);
				uni.navigateTo({
					url: url
				})
			}
		}
	}
</script>
<style lang="scss">
	page {
		position: relative;
		background-color: #f2f2f2;
	}

	@font-face {
		font-family: "HMfont-home";
		src: url('data:application/x-font-woff2;charset=utf-8;base64,') format('woff2');
	}

	.icon {
		font-family: "HMfont-home" !important;
		font-size: 60upx;
		font-style: normal;
		color: #000000;

		&.setting {
			&:before {
				content: "\e64e";
			}
		}

		&.tongzhi {
			&:before {
				content: "\e729";
			}
		}

		&.qr {
			&:before {
				content: "\e600";
			}
		}

		&.chongzhi {
			&:before {
				content: "\e648";
			}
		}

		&.fukuan {
			&:before {
				content: "\e67f";
			}
		}

		&.fahuo {
			&:before {
				content: "\e680";
			}
		}

		&.shouhuo {
			&:before {
				content: "\e6b1";
			}
		}

		&.pingjia {
			&:before {
				content: "\e66b";
			}
		}

		&.tuihuo {
			&:before {
				content: "\e66c";
			}
		}
	}

	.status {
		width: 100%;
		height: 0;
		position: fixed;
		z-index: 10;
		background-color: #ff8000;
		top: 0;
		/*  #ifdef  APP-PLUS  */
		height: var(--status-bar-height); //覆盖样式
		/*  #endif  */

	}

	.header {
		width: 92%;
		padding: 0 4%;
		height: 100upx;
		display: flex;
		justify-content: flex-end;
		align-items: center;
		position: fixed;
		top: 0;
		z-index: 10;
		background-color: #ff8000;
		/*  #ifdef  APP-PLUS  */
		top: var(--status-bar-height);

		/*  #endif  */
		.icon-btn {
			width: 120upx;
			height: 60upx;
			flex-shrink: 0;
			display: flex;

			.icon {
				color: #fff;
				width: 60upx;
				height: 60upx;
				display: flex;
				justify-content: flex-end;
				align-items: center;
				font-size: 42upx;
			}
		}
	}

	.place {
		background-color: #ff8000;
		height: 100upx;
		/*  #ifdef  APP-PLUS  */
		margin-top: var(--status-bar-height);
		/*  #endif  */
	}

	.place-bottom {
		height: 300upx;
	}

	.user {
		width: 92%;
		padding: 0 4%;
		display: flex;
		align-items: center;
		// position: relative;
		background-color: #ff8000;
		padding-bottom: 30upx;
		overflow: hidden;

		.headView {
			display: flex;
			justify-content: center;
			align-items: center;
			// margin-top: 50rpx;
			padding: 0 70rpx 0 50rpx;
			width: 20vw;
			height: 20vw;
			position: relative;

			.icon {
				position: absolute;
				width: 20vw;
				height: 20vw;
				border-radius: 50%;
				border: 32upx solid #ff8000;
			}

			.iconBorder {
				position: absolute;
				width: 20vw;
				height: 20vw;
				border-radius: 50%;
				border: solid 1upx #fff;
			}
		}

		.left {
			width: 20vw;
			height: 20vw;
			flex-shrink: 0;
			margin-right: 20upx;
			border: solid 1upx #fff;
			border-radius: 100%;

			image {
				width: 20vw;
				height: 20vw;
				border-radius: 100%;
			}

		}

		.right {
			width: 100%;

			.username {
				font-size: 36upx;
				color: #fff;
			}

			.signature {
				color: #eee;
				font-size: 28upx;
			}
		}

		.erweima {
			flex-shrink: 0;
			width: 10vw;
			height: 10vw;
			margin-left: 5vw;
			border-radius: 100%;

			display: flex;
			justify-content: center;
			align-items: center;
			background: linear-gradient(to left, #fbbb37 0%, #fcf0d0 105%);

			.icon {
				color: #7b6335;
				font-size: 42upx;
			}
		}
	}

	.order {
		// width: 84%;
		// margin: 20upx 4% 20upx 4%;
		margin-bottom: 14upx;
		padding: 20upx 0;
		background-color: #fff;
		// box-shadow: 0upx 0upx 25upx rgba(0, 0, 0, 0.1);
		// border-radius: 15upx;

		.list {
			display: flex;
			// border-bottom: solid 1upx #17e6a1;
			padding: 10upx;

			.box {
				width: 25%;

				.img {
					width: 100%;
					display: flex;
					justify-content: center;

					.icon {
						font-size: 50upx;
						color: #464646;
					}
				}

				.text {
					width: 100%;
					display: flex;
					justify-content: center;
					font-size: 24upx;
					color: #3d3d3d;
				}
			}
		}

		.balance-info {
			display: flex;
			padding: 10upx 0;

			.left {
				width: 75%;
				display: flex;

				.box {
					width: 50%;
					font-size: 28upx;

					.num {
						width: 100%;
						height: 50upx;
						display: flex;
						justify-content: center;
						align-items: flex-end;
						color: #000;
						font-size: 34upx;
					}

					.text {
						width: 100%;
						display: flex;
						justify-content: center;
						color: #3d3d3d;
						font-size: 24upx;
					}
				}
			}

			.right {
				border-left: solid 1upx #e2e2e2;
				width: 25%;

				.box {

					.img {
						width: 100%;
						height: 50upx;
						display: flex;
						justify-content: center;
						align-items: flex-end;

						.icon {
							font-size: 45upx;
							color: #3d3d3d;
						}
					}

					.text {
						width: 100%;
						display: flex;
						justify-content: center;
						font-size: 24upx;
						color: #3d3d3d;
					}
				}
			}
		}
	}

	.VIP {
		// width: 84%;
		margin: 0 auto;
		padding: 20upx 4%;
		background-color: #fff;
		// background: linear-gradient(to left, #dea96d 0%, #f6d59b 100%);
		// box-shadow: 0upx 0upx 25upx rgba(0, 0, 0, 0.2);
		// border-radius: 15upx;
		border-bottom: #eeeeee solid 1upx;
		display: flex;
		align-items: center;

		.title {
			width: 100%;
			color: #796335;
			font-size: 30upx;
		}

		.tis {
			width: 100%;
			display: flex;
			justify-content: flex-end;
			color: #c2c2c2;
			font-size: 26upx;
		}
	}

	.toolbar {
		width: 92%;
		margin: 0 4% 0 4%;
		padding: 0 0 20upx 0;
		background-color: #fff;
		box-shadow: 0upx 0upx 25upx rgba(0, 0, 0, 0.1);
		border-radius: 15upx;

		.title {
			padding-top: 10upx;
			margin: 0 0 10upx 3%;
			font-size: 30upx;
			height: 80upx;
			display: flex;
			align-items: center;
		}

		.list {
			display: flex;
			flex-wrap: wrap;

			.box {
				width: 25%;
				margin-bottom: 30upx;

				.img {
					width: 23vw;
					height: 10.5vw;
					display: flex;
					justify-content: center;

					image {
						width: 9vw;
						height: 9vw;
					}
				}

				.text {
					width: 100%;
					display: flex;
					justify-content: center;
					font-size: 26upx;
					color: #3d3d3d;
				}
			}
		}
	}
</style>
